}

.bottom-bar{
    position: relative;
    width: 40px;
    height: 7px;
    background: #FFF;
    margin-top: 5px
}

.topBarXOpen{
    -webkit-animation: topBarXOpen .5s forwards;
            animation: topBarXOpen .5s forwards;
}

.midBarOpen{
    -webkit-animation: middleBarOpen .5s forwards;
            animation: middleBarOpen .5s forwards;
}

.bottomBarXOpen{
    -webkit-animation: bottomBarXOpen .5s forwards;
            animation: bottomBarXOpen .5s forwards;
}

.topBarXClose{
    -webkit-animation: topBarXClose .5s forwards;
            animation: topBarXClose .5s forwards;
}

