    -webkit-animation: middleBarClose .5s forwards;
            animation: middleBarClose .5s forwards;
}

.bottomBarXClose{
    -webkit-animation: bottomBarXClose .5s forwards;
            animation: bottomBarXClose .5s forwards;
}

.menu {
