    overflow: scroll;
}

.slide-in {
    -webkit-animation: slideIn .5s forwards;
            animation: slideIn .5s forwards;
}

.slide-out {
    -webkit-animation: slideOut .5s forwards;
            animation: slideOut .5s forwards;
}

.show {
    display: block;
    min-width: 250px;
}

/* Hamburger Menu Animations */
@-webkit-keyframes middleBarOpen {
    from {
        opacity: 1;
    }
    to {
        opacity: 0;
    }
}
@keyframes middleBarOpen {
    from {
        opacity: 1;
    }
    to {
        opacity: 0;
    }
}

@-webkit-keyframes middleBarClose {
    from {
        opacity: 0;
    }
    to {
        opacity: 1;
    }
}

@keyframes middleBarClose {
    from {
        opacity: 0;
    }
    to {
        opacity: 1;
    }
}

@-webkit-keyframes topBarXOpen {
    0% {
        top: 0;
        -webkit-transform: rotate(0deg);
                transform: rotate(0deg);
    }
    35%{
        top: 13px;
    }
    50% {
        -webkit-transform: rotate(0deg);
                transform: rotate(0deg);
        top: 13px;
    }
    100% {
        top: 13px;
        -webkit-transform: rotate(45deg);
                transform: rotate(45deg);
    }
}

@keyframes topBarXOpen {
    0% {
        top: 0;
        -webkit-transform: rotate(0deg);
                transform: rotate(0deg);
    }
    35%{
        top: 13px;
    }
    50% {
        -webkit-transform: rotate(0deg);
                transform: rotate(0deg);
        top: 13px;
    }
    100% {
        top: 13px;
        -webkit-transform: rotate(45deg);
                transform: rotate(45deg);
    }
}

@-webkit-keyframes topBarXClose {
    0% {
        top: 13;
        -webkit-transform: rotate(45deg);
                transform: rotate(45deg);
    }
    50%{
        top: 13px;
        -webkit-transform: rotate(0deg);
                transform: rotate(0deg);
    }
    65% {
        -webkit-transform: rotate(0deg);
                transform: rotate(0deg);
        top: 0px;
    }
    100% {
        top: 0px;
        -webkit-transform: rotate(0deg);
                transform: rotate(0deg);
    }
}

@keyframes topBarXClose {
    0% {
        top: 13;
        -webkit-transform: rotate(45deg);
                transform: rotate(45deg);
    }
    50%{
        top: 13px;
        -webkit-transform: rotate(0deg);
                transform: rotate(0deg);
    }
    65% {
        -webkit-transform: rotate(0deg);
                transform: rotate(0deg);
        top: 0px;
    }
    100% {
        top: 0px;
        -webkit-transform: rotate(0deg);
                transform: rotate(0deg);
    }
}

@-webkit-keyframes bottomBarXOpen {
    0% {
        bottom: 0;
        -webkit-transform: rotate(0deg);
                transform: rotate(0deg);
    }
    35%{
        bottom: 11px;
    }
    50% {
        -webkit-transform: rotate(0deg);
                transform: rotate(0deg);
        bottom: 11px;
    }
    100% {
        -webkit-transform: rotate(-45deg);
                transform: rotate(-45deg);
        bottom: 11px;
    }
}

@keyframes bottomBarXOpen {
    0% {
        bottom: 0;
        -webkit-transform: rotate(0deg);
                transform: rotate(0deg);
    }
    35%{
        bottom: 11px;
    }
    50% {
        -webkit-transform: rotate(0deg);
                transform: rotate(0deg);
        bottom: 11px;
    }
    100% {
        -webkit-transform: rotate(-45deg);
                transform: rotate(-45deg);
        bottom: 11px;
    }
}

@-webkit-keyframes bottomBarXClose {
    0% {
        bottom: 11px;
        -webkit-transform: rotate(-45deg);
                transform: rotate(-45deg);
    }
    50%{
        bottom: 11px;
    }
    65% {
        -webkit-transform: rotate(0deg);
                transform: rotate(0deg);
        bottom: 0px;
    }
    100% {
        -webkit-transform: rotate(0deg);
                transform: rotate(0deg);
        bottom: 0px;
    }
}

@keyframes bottomBarXClose {
    0% {
        bottom: 11px;
        -webkit-transform: rotate(-45deg);
                transform: rotate(-45deg);
    }
    50%{
        bottom: 11px;
    }
    65% {
        -webkit-transform: rotate(0deg);
                transform: rotate(0deg);
        bottom: 0px;
    }
    100% {
        -webkit-transform: rotate(0deg);
                transform: rotate(0deg);
        bottom: 0px;
    }
}

/* Menu Animations */
@-webkit-keyframes slideIn {
    from {
        width: 0;
    }
    to {
        width: 50vw;
    }
}
@keyframes slideIn {
    from {
        width: 0;
    }
    to {
        width: 50vw;
    }
}

@-webkit-keyframes slideOut {
    from {
        width: 50vw;
    }
    to {
        width: 0;
    }
}

@keyframes slideOut {
    from {
        width: 50vw;
    }
    to {
        width: 0;
    }
}

